"use client";
import { useState } from "react";
import { Navbar } from "../components/Navbar"
import { Footer } from "../components/Footer"
import RPGSheet from "../components/RPGSheet"
import MaskedImageUpload from "../components/MaskedImageUpload"
import ImageMaskControls from "../components/ImageMaskControls"


export default function Home() {
  const [posX, setPosX] = useState(50)
  const [posY, setPosY] = useState(50)
  const [zoom, setZoom] = useState(100)

  return (
    <>
      <Navbar />
      <main className="flex-1 bg-background py-8 px-4">
        <div className="mx-auto max-w-7xl">
          <div className="flex justify-center gap-4">
            <section id="sheet">
              <RPGSheet />
            </section>
            <div>
              <section>
                <ImageMaskControls
                  posX={posX}
                  posY={posY}
                  zoom={zoom}
                  onPosXChange={setPosX}
                  onPosYChange={setPosY}
                  onZoomChange={setZoom}
                />
              </section>
              <section id="upload">
                <MaskedImageUpload
                  className="imgUpload masked-image"
                  posX={posX}
                  posY={posY}
                  zoom={zoom}
                />
              </section>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  )
}
